<template>
  <el-drawer
    :model-value="visible"
    title="高级筛选"
    size="550"
    class="custom-drawer"
    :before-close="handleClose"
  >
    <el-form label-position="top">
      <el-form-item label="通话时间">
        <el-date-picker
          v-model="formData.dateList"
          type="daterange"
          range-separator="~"
          :clearable="false"
          format="YYYY-MM-DD"
          @change="changeDate"
          @calendar-change="
            (dateList) => {
              disabledDateInstance.handleCalendar(dateList);
            }
          "
          :disabled-date="
            (time) => {
              return disabledDateInstance.handleDisabled(time);
            }
          "
        />
      </el-form-item>
      <el-form-item label="主叫号码">
        <el-input v-model="formData.callerNum" placeholder="请输入" clearable />
      </el-form-item>
      <el-form-item label="中间号">
        <el-input v-model="formData.bindNum" placeholder="请输入" clearable />
      </el-form-item>
      <el-form-item label="被叫号码">
        <el-input v-model="formData.calleeNum" placeholder="请输入" clearable />
      </el-form-item>

      <el-form-item label="通话状态">
        <el-select v-model="formData.status" placeholder="请选择" clearable>
          <el-option
            v-for="item in callStatusList"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          />
        </el-select>
      </el-form-item>
    </el-form>
    <template #footer>
      <el-button @click="handleClose">关闭</el-button>
      <el-button type="primary" @click="handleSearch">搜索</el-button>
    </template>
  </el-drawer>
</template>
<script lang="ts" setup>
import { reactive } from "vue";

import { callStatusList } from "@assets/typeStatus";
import { dateStartOf, dateEndOf } from "@/assets/common";
import { DisabledDate } from "@assets/instanceCollection";

defineProps<{
  visible: boolean;
}>();
const emits = defineEmits<{
  (e: "update:visible", value: boolean);
  (e: "emitFunc", value: Record<string, any>);
}>();
const formData = reactive({
  calleeNum: "",
  callerNum: "",
  bindNum: "",
  status: "",
  dateList: [dateStartOf(), dateEndOf()],
});
const disabledDateInstance = new DisabledDate(7);
const handleSearch = () => {
  emits("emitFunc", formData);
  handleClose();
};
// 关闭弹框
const handleClose = () => {
  emits("update:visible", false);
  formData.calleeNum = "";
  formData.callerNum = "";
  formData.bindNum = "";
  formData.status = "";
  formData.dateList = [dateStartOf(), dateEndOf()];
};
// 选择日期
const changeDate = (value) => {
  if (value) {
    formData.dateList = [dateStartOf(value[0]), dateEndOf(value[1])];
  } else {
    formData.dateList = [dateStartOf(), dateEndOf()];
  }
};
</script>
<style lang="scss" scoped>
:deep(.el-select) {
  width: 100%;
}
</style>
